.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: $header-bg-color;
  font-weight: 600;
  text-transform: uppercase;
  overflow: auto;
  z-index: 999999;

  @include content-padding-inline;

  &__icon-close {
    display: block;
    height: 24px;
    width: 24px;
    background-size: cover;

    background-image: url(../images/icons/icon-close.png);

    @include hover(transform, scale(1.1));
  }


  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__block {
    margin-top: 45px;
    margin-bottom: 24px;
    position: sticky;
    top: 0;
    z-index: 1;
  }

  &__nav {

    &-list {
    margin: 0;
    padding-top: 48px;
    padding-left: 0;
  }

  &-item {
  list-style-type:  none;
  padding-bottom: 24px;
}

&-link {
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 19.5px;
  letter-spacing: 3px;
  text-align: left;
  color: $color-for-dark-bg;
  text-decoration: none;
  text-transform: uppercase;
}
}
}
